* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  max-width: 960px;
  margin: 0 auto;
  font-family: Georgia, Times New Roman, Times, serif;
}

.form-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.register-section, .login-section {
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
}

form {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

.form-header {
  text-align: center;
}

label {
  font-weight: bold;
}

input[type="text"], input[type="password"], input[type="email"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 5px 0 15px;
  padding: 10px;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin: 8px 0;
  padding: 14px 20px;
}

input[type="submit"]:hover {
  background-color: #45a049;
}

#errMessageReg, #errMessageLog {
  color: red;
  text-align: center;
  margin-top: 10px;
}
